#dialogOverlay {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    background: rgba($main-font-color, 0.5);
}
#bgContent {
    display: flex;
    justify-content: space-between;
    width: 980px;
    height: 330px;
    margin:96px 230px 534px 230px;
}
#dialogBox {
    display: none;
    background-color: white;
    position: absolute;
    width: 920px;
    height: 210px;
    z-index: 200;

    span#close {
        position: absolute;
        right: 46px;
        top: 46px;
        font-size: 30px;
    }

    span#close:hover {
        cursor: pointer;
    }

    #boxContent {
        width: 380px;
        height: 98px;
        left: 355px;
        top: 431px;
        margin: 56px 445px 56px 95px;

        #reset {
            display: none;
            position: absolute;
            left: 250px;
            top: 56px;
            @include button;
        }

        h2 {
            @include big;
            margin: 0;
        }

        p#alert {
            @include small;
            height: 24px;
            margin-top: 24px;
            margin-bottom: 0;

            span {
                font-weight: 600;
            }
        }
    }
}
